body {
  margin: 0;
  line-height: normal;
}

.popup-overlay {
  display: flex;
  flex-direction: column;
  position: fixed;
  inset: 0;
}

:root {
  /* fonts */
  --font-inter: Inter;
  --font-langar: Langar;
  --font-lato: Lato;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-base: 16px;
  --font-size-xl: 20px;
  --font-size-11xl: 30px;
  --font-size-lg: 18px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-gainsboro-100: #d9d9d9;
  --color-peru-100: #c27f2f;
  --color-peru-200: rgba(194, 127, 47, 0.75);
  --color-gray-100: #362310;
  --color-darkorange-100: #ff7a00;

  /* border radiuses */
  --br-6xl: 25px;
  --br-341xl: 360px;
}
